body {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  .shirts-list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    justify-content: center;
    padding: 50px;
  }
  
  .shirt-item {
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    transition: transform 0.2s;
  }
  
  .shirt-item:hover {
    transform: scale(1.05);
  }
  
  .shirt-image {
    width: 100%;
    height: auto;
  }
  
  .shirt-price {
    font-size: 1.2em;
    color: #333;
    margin: 10px 0;
  }
  
  .add-to-cart-button {
    background-color: #0073bb;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s;
  }
  
  .add-to-cart-button:hover {
    background-color: #005bb5;
  }
  
  .size-selector {
    margin-bottom: 20px;
    text-align: center;
    padding-top: 100px;
  }
  
  .size-selector select {
    padding: 10px;
    font-size: 1em;
  }
  .modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
    width: 300px;
  }
  